@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.errorRow {
  margin: 15px;
  text-align: center;
  color: var(--error);
  font-weight: 800;
}

.errorText {
  color: var(--error);
}

.login-container {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  background-image: url('/login_bg.jpg');
  background-repeat:no-repeat;
  background-position: center center;
}

.logo-login {
  height: 40px;
  width: auto;
  margin-right: 10px;
}

.login-container > h1 {
  font-size: 1.9rem;
  margin: 0 0 100px;
  padding: 15px;
}

.info {
  font-size: 1.1rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  color: var(--main-very-dark);
  text-shadow: 1px 1px var(--main-very-dark);
  margin: 20px;
}

.connect-to-yahoo {
  margin-top: 50px;
}

.connect-to-yahoo a {
  background-color: var(--main-dark);
  color: var(--main-light);
  border: 2px solid var(--main-light);
  border-radius: 4px;
  padding: 10px;
  font-weight: bold;
  text-decoration: none;
}

.connect-to-yahoo a:hover {
  background-color: var(--main-even);
  color: white;
}

footer {
  position: fixed;
  bottom: 10px;
  right: 10px;
  background: var(--main-light);
  font-size: 0.6rem;
  padding: 4px;
  border-radius: 2px;
}

ul {
  display: flex;
  justify-content: space-around;
}

li.react-tabs__tab {
  padding: 8px 10px;
  position: relative;
  border: 1px solid var(--gradient1);
  border-radius: 8px 8px 0 0;
  border-bottom: none;
  bottom: 10px;
  cursor: pointer;
  background: rgb(215, 235, 245);
  background: var(--main-light);
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-weight: 900;
}

.react-tabs__tab-panel {
  transition: background 0.2s ease;
  padding: 0 20px;
  margin-top: 0;
}

#react-tabs-1 > div {
  padding-top: 20px;
}

#react-tabs-3 > div {
  padding-top: 18px;
}

#react-tabs-5 > div {
  padding-top: 16px;
}

#react-tabs-7 > div {
  padding-top: 14px;
}

/* Admin tabs */

#react-tabs-15 {
  background: var(--main-light);
  padding: 0;
}

#react-tabs-17 > div {
  padding-top: 20px;
}

#react-tabs-19 > div {
  padding-top: 18px;
}

#react-tabs-21 > div {
  padding-top: 16px;
}

#react-tabs-23 > div {
  padding-top: 14px;
}

.react-tabs__tab:hover {
  background: var(--main-even);
  transition: background 0.1s ease;
}

li.react-tabs__tab--selected:hover {
  cursor: auto;
  background: var(--main-dark);
}

li.react-tabs__tab--selected, .react-tabs__tab-panel {
  border: 1px solid transparent;
  background: var(--main-dark);
  color: rgb(245, 245, 245);
  transition: background 0.1s ease;
  font-weight: 900;
}

.navbar-tabs {
  padding: 20px 0 10px 0;
  font-size: 1.2rem;
  width: calc(100vw - 250px);
  min-width: 550px;
}

.react-tabs__tab-list {
  max-width: 100%;
}

.react-tabs__tab--selected {
  font-weight: bold;
  position: fixed;
}

.navbar-tabs > div {
  position: relative;
  width: 100%;
  top: -10px;
}

.team-logo-container {
  margin: 0;
  display: flex;
  place-content: center;
}

.teamLogo {
  height: 35px;
  width: auto;
}

@media screen and (max-width: 800px) { 
  .navbar-tabs {
    width: auto;
    min-width: 350px;
  }

  /* Players tab needs to scroll to see all stats */
  #react-tabs-3 > div {
    overflow: scroll;
  }

  #react-tabs-12, #react-tabs-14 {
    display: none;
  }

  .navbar-tabs > div, .navbar-tabs > ul {
    width: 100vw;
  }

  ul.react-tabs__tab-list:last-child, li.react-tabs__tab > div {
    display: none;
  }

  .react-tabs__tab-panel {
    padding: 0;
  }

  th {
    width: auto;
  }
}

.search-filter {
  width: 100%;
  margin: 0;
}

.team-filter, .position-filter {
  width: 50px;
}

@media screen and (max-width: 800px) { 
  .team-filter, .position-filter {
    width: 30px;
  }
}

table {
  box-shadow: -3px -3px 5px var(--main-even);;
  border-spacing: 0;
  text-align: left;
  border-radius: 4px;
  padding: 0; 
  font-size: 0.9rem;
  margin: 0;
  width: 100vw;
  max-width: 100%;
  padding-right: 22vw;
  -webkit-column-width: auto;
          column-width: auto;
}

.table thead th {
  vertical-align: top;
  font-size: 1.1rem;
  padding: 4px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  bottom: 0;
  background-color: var(--main-even);
}

.table td {
  padding: 4px;
  vertical-align: inherit;
}

thead, tbody {
  max-width: 100%;
}

tr:nth-child(odd) {
  background-color: rgba(164, 189, 205, 0.7);
  background-color: var(--main-even);
}

tr:nth-child(even) {
  background-color: rgba(200, 233, 255, 0.7);
  background-color: var(--main-even2);
}

tr:hover {
  background-color: rgb(166, 220, 250);
}

th, td {
  margin: 0;
  padding: 2px;
  border-bottom: 1px solid var(--main-very-dark);
  border-right: 1px solid var(--main-very-dark);
}

td:last-child, th:last-child {
  border-right: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

.blank-cell {
  background-color: transparent;
}

.change-user-dropdown {
  width: 100px;
}

.disabled-pick {
  background-color: #de7d7d !important;
}

.taken-player {
  background-color: #b69d9d !important;
}

.small-username {
  font-size: 0.7rem;
}

.player-name-and-headshot {
  display: flex;
  align-items: center;
  line-height: 1.3;
}

.headshot {
  height: 35px;
  width: auto;
  margin: 0 10px 0 0; 
  border-radius: 12px;
}

.timezoneInWords {
  font-size: 0.8rem;
}

.prospect {
  font-size: 0.8rem;
  background: lightcyan;
  border: 1px solid var(--main-very-dark);
  border-radius: 100%;
  padding: 4px 7px;
}

.keeper {
  font-size: 0.8rem;
  background: var(--main-very-dark);
  color: white;
  border-radius: 2px;
  padding: 3px 6px;
}

#prospect-column, #team-dropdown-column, #show-taken-dropdown {
  background: transparent;
  position: absolute;
  height: 50px;
  top: 0;
  left: 200px;
  border: none;
  color: var(--main-light);
}

.logo-container {
  width: 60px;
  margin-left: 33%;
}

#show-taken-dropdown {
  left: 360px;
}

.prospect-column-hidden {
  width: 0;
  padding: 0 !important;
  border: none;
}

.asterisk {
  color: grey;
  font-size: 1.1rem;
  font-weight: 700;
}

@media screen and (max-width: 800px) { 
  table {
    width: auto;
    min-width: 350px;
    max-width: 100vw;
    margin: 0;
    padding: 0;
  }
  th.blank-cell, .admin-column {
    display: none;
  }
  .table thead th, .table td {
    font-size: 0.7rem;
    padding: 3px;
  }
  .headshot {
    display: none;
  }
  .draftPickTimestamp {
    font-size: 0.7rem;
  }
  .timezoneInWords {
    display: none;
  }
  .teamLogo {
    height: 25px;
    width: auto;
  }
  .team-logo-container {
    height: 100%;
    min-height: 50px;
    align-items: center;
  }
  #prospect-column, #team-dropdown-column, #show-taken-dropdown {
    top: 12px;
    left: 90px;
  }
  .player-count {
    margin: 5px 0 0 220px;
    width: 140px;
    font-size: 1.1rem;
  }
  #show-taken-dropdown {
    left: 210px;
  }
  .prospect {
    font-size: 0.6rem;
    padding: 1px 4px;
  }
  .keeper {
    font-size: 0.7rem;
    padding: 2px 3px;
  }
}


.pagination {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 590px;
  padding: 0;
  margin: 0 0 20px 0;
}

.round {
  font-weight: bold;
  font-size: 1.7rem !important;
  color: white;
}

.pagination-arrows {
  display: flex;
  color: var(--main-very-dark) !important;
  cursor: pointer;
}

.pagination-arrows span {
  color: #000 !important;
}

.pagination select {
  height: 33px;
  width: 250px;
}

.pagination li.pagination-goto-page {
  display: flex;
  flex-direction: row;
  border-radius: 4px;
}

.page-link {
  display: flex;
  color: var(--main-very-dark);
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.page-of-page {
  width: 70px;
}

.pagination-goto-page a {
  width: 200px;
}

.pagination-goto-page .page-link {
  border-radius: 4px;
}

.pagination li {
  font-size: 0.9rem;
}

.current-round-button {
  width: 50px;
  height: 18px;
  font-size: 0.9rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  line-height: 0.8;
  margin: 0 4px;
}

@media (max-width: 800px) {
  .pagination {
    padding-right: 0;
    margin-right: 0;
    min-width: 300px;
  }

  .pagination li.pagination-goto-page {
    font-size: 0.7;
  }

  .page-link {
    font-size: 1.4rem;
    padding: 2px 10px;
    margin: 0 5px 10px;
  }

  .current-round-button {
    line-height: 1;
    font-size: 0.9rem;
    height: 28px;
  }

  .page-first {
    margin-left: 20px !important;
    padding: 7px 2px;
  }

  .page-last {
    padding: 7px 4px;
  }

  .page-first, .page-last {
    font-feature-settings: "c2sc";
    font-variant: small-caps;
    font-size: 0.9rem;
  }

  .page-or-round {
    font-feature-settings: "c2sc";
    font-variant: small-caps;
    margin-left: -30px;
    font-size: 1.1rem;
    height: 35px;
    padding-top: 6px;
  }
}

.loading-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
  transition: background 0.15s ease;
  height: 100vh;
  width: calc(100vw - 250px);
  color: whitesmoke;
  padding-bottom: 20vh;
  z-index: 99;
}

.loading-gif {
  display: flex;
  justify-content: center;
  align-items: center;
}

.absolute {
  position: absolute;
}

@media screen and (max-width: 800px) {   
  .loading-wrapper {
    width: auto;
  }
}

.ReactModal__Content ReactModal__Content--after-open {
  min-width: 500px;
  height: 80vh;
  min-height: 400px; 
