    margin: 0 !important;
  }

  #new-forum-post-modal textarea, #new-rule-modal textarea {
    height: 200px;
    padding: 5px;
  }

  .modal-title, .modal-player-info, .button-group {
    padding: 5px;
    margin: 25px 5px;
    font-size: 1.1rem;
  }

  .button-group > button {
    margin: 10px;
    padding: 5px;
  }

}


.user-wrapper {
  display: flex;
