  margin: 10px;
}

.user-in-draft {
  display: flex;
  align-items: center;
  font-size: 0.7rem;
  border-radius: 0 8px 8px 0;
  padding: 0 4px;
  text-align: center;
  color: var(--main-very-dark);
  font-weight: bold;
  background: var(--main-even2);
  border: 1px solid var(--main-light);
}

.user-logo {
  height: 30px;
