  text-align: left;
  border-bottom: 1px solid grey;
  overflow-y: scroll;
}

#user-list > span {
  font-weight: 400;
}

.user-wrapper {
  display: flex;
}

.narrow-user-wrapper {
  margin: 1px;
  height: 20px;
}

.user {
  color: darkorchid;
  border-radius: 0 8px 8px 0;
  font-weight: bold;
  font-size: 0.7rem;
  margin: 4px;
  padding: 4px;
  background: var(--gradient3);
}

.user-in-chat {
  margin: 0 4px 0 0;
  padding: 3px 3px 2px;
}

.user-icon {
  font-size: 1.1rem;
  border-radius: 8px 0 0 8px;
  padding: 2px !important;
  height: 30px;
}

.narrow-icon {
  padding: 1px !important;
  font-size: 0.85rem;
}

.message {
  word-break: break-all;
  word-break: break-word;
  font-size: 0.8rem;
}

.chat-announcement {
  font-weight: bold;
  font-style: italic;
}

#messageInput {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 250px;
  padding: 5px;
}

#send-message-button {
  position: fixed;
  right: -200px;
  /* display: none; */
}


@media (max-width: 800px) {
  #chatbox {
    display: none;
  }
}

#countdown {
	color: red;
	border: none;
	border-radius: 8px;
	background-color: whitesmoke;
	position: relative;
	right: 2px;
	bottom: 10px;
	padding: 0 5px;
	font-weight: bold;
	font-size: 1.4rem;
}

.drafting-now {
	background: whitesmoke;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-right: 2px solid grey;
  border-left: 2px solid var(--main-very-dark);
	width: 65%;
	min-width: 140px;
  border-bottom: none;
	padding: 5px 0 0 5px;
	text-align: left;
}

.drafting-1, .drafting-true {
	background-color: #7ef20b7a;
}

.drafting-now :first-child {
  font-weight: bold;
  padding: 0 5px 0 0;
  margin: 0;
}

@media (max-width: 800px) {
  .drafting-now {
		flex-direction: row;
    width: 100vw;
    display: flex;
		border: none;
		justify-content: left;
		padding-left: 10px;
	}

	.drafting-now :first-child {
		margin-bottom: 12px;
	}
	
	#countdown {
		position: static;
		min-width: 90px;
		max-width: 110px;
	}
}

.widget-wrapper {
  background-color: transparent;
  position: fixed;
  display: flex;
  flex-direction: row;
  top: 0;
  right: 0;
  width: 270px;
}

#logout {
  /* position: fixed; */
  top: 5px;
  right: 12px;
  z-index: 99;
}

.logo-and-logout-wrapper {
  background-color: whitesmoke;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  height: 90px;
  width: 35%;
  align-items: center;
  margin-right: 5px;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  top: 35px;
  right: 28px;
}

.logo {
  width: 30px;
  height: auto;
}

@media (max-width: 800px) {
  .widget-wrapper {
    height: 60px;
    width: auto;
    background-color: white;
	}
  
  .logo-and-logout-wrapper {
    display: relative;
    width: 0;
  }

  .logo-wrapper {
    display: none;
  }

  #logout {
    position: fixed !important;
    top: 12px;
    right: 2px;
    font-size: 0.7rem;
  }
}

:root {
  --main-bg: #4484CE;
  --main-text:  #1b2733;
  --gradient1: #D9D9D9;
  --orange: #F19F4D;
  --yellow: #F9CF00;

  --main-very-dark: rgb(9, 1, 56);
  --main-dark: #4484CE;
  --main-even: #a1c1e6;
  --main-even2: #7da5d3;
  --main-light: #D9D9D9;
  --alt-text: var(--yellow);

  --link: var(--main-very-dark);


  /* --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5); */

  --header-text: #ffffff;
  --button-bg: var(--main-dark);
  --disabled:rgba(87, 87, 128, 0.5);
  --success: #00ff99;
  --success-hover: #1cba7b;
  --error: rgba(238, 11, 11, 0.651);
  --error-hover: rgba(197, 31, 78, 0.651);
}

/* 🎨 colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: flex;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: #D9D9D9;
  background: var(--main-light);;    
  /* grid-template-columns: minmax(100%, 10%); */
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  margin: 0;
  width: calc(100vw - 270px);
  /* padding-right: 22vw; */
  min-width: 600px;
  /* margin: 0 auto; */
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

a {
  color: rgb(9, 1, 56);
  color: var(--link);
}

a:hover {
  color: #4484CE;
  color: var(--main-dark);
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  margin: 50px auto;
  padding: 5px;
  background: #4484CE;
  background: var(--main-dark);
  max-width: 100%;
  color: #D9D9D9;
  color: var(--main-light);;
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: #ffffff;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: #4484CE;
  color: var(--main-dark);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

button {
  border-radius: 4px;
  padding: 4px;
  border: 1px solid #D9D9D9;
  border: 1px solid var(--main-light);
  color: #D9D9D9;
  color: var(--main-light);
  font-weight: 800;
  background: #4484CE;
  background: var(--button-bg);
}

button:disabled {
  background-color: #D9D9D9;
  background-color: var(--main-light);
  color: rgba(87, 87, 128, 0.5);
  color: var(--disabled);
}

.save-button {
  max-width: 100px;
  margin: 15px auto;
  color: #1b2733;
  color: var(--main-text);
}

.save-button:not(:disabled) {
  color: #D9D9D9;
  color: var(--main-light);
  background:#4484CE;
  background:var(--main-dark);
}

.button-large {
  font-size: 1.4rem;
  padding: 7px 15px;
}

button:not(:disabled):hover {
  background: #a1c1e6;
  background: var(--main-even);
  border: 1px solid #a1c1e6;
  border: 1px solid var(--main-even);
  color: rgb(9, 1, 56);
  color: var(--main-very-dark);
}

.hidden {
  display: none;
}

.margin-15 {
  margin: 15px;
}

.margin-50 {
  margin: 50px;
}

textarea {
  width: 90%;
}

.new-rules-button {
  margin-bottom: -25px;
}

@media (max-width: 800px) {
  main {
    margin-top: 55px;
    width: 100vw;
    min-width: 350px;
    overflow-x: hidden;
  }

  main > div {
    width: 100%;
  }
}

