ul {
  display: flex;
  justify-content: space-around;
}

li.react-tabs__tab {
  padding: 8px 10px;
  position: relative;
  border: 1px solid var(--gradient1);
  border-radius: 8px 8px 0 0;
  border-bottom: none;
  bottom: 10px;
  cursor: pointer;
  background: rgb(215, 235, 245);
  background: var(--main-light);
  font-variant: small-caps;
  font-weight: 900;
}

.react-tabs__tab-panel {
  transition: background 0.2s ease;
  padding: 0 20px;
  margin-top: 0;
}

#react-tabs-1 > div {
  padding-top: 20px;
}

#react-tabs-3 > div {
  padding-top: 18px;
}

#react-tabs-5 > div {
  padding-top: 16px;
}

#react-tabs-7 > div {
  padding-top: 14px;
}

/* Admin tabs */

#react-tabs-15 {
  background: var(--main-light);
  padding: 0;
}

#react-tabs-17 > div {
  padding-top: 20px;
}

#react-tabs-19 > div {
  padding-top: 18px;
}

#react-tabs-21 > div {
  padding-top: 16px;
}

#react-tabs-23 > div {
  padding-top: 14px;
}

.react-tabs__tab:hover {
  background: var(--main-even);
  transition: background 0.1s ease;
}

li.react-tabs__tab--selected:hover {
  cursor: auto;
  background: var(--main-dark);
}

li.react-tabs__tab--selected, .react-tabs__tab-panel {
  border: 1px solid transparent;
  background: var(--main-dark);
  color: rgb(245, 245, 245);
  transition: background 0.1s ease;
  font-weight: 900;
}

.navbar-tabs {
  padding: 20px 0 10px 0;
  font-size: 1.2rem;
  width: calc(100vw - 250px);
  min-width: 550px;
}

.react-tabs__tab-list {
  max-width: 100%;
}

.react-tabs__tab--selected {
  font-weight: bold;
  position: fixed;
}

.navbar-tabs > div {
  position: relative;
  width: 100%;
  top: -10px;
}

.team-logo-container {
  margin: 0;
  display: flex;
  place-content: center;
}

.teamLogo {
  height: 35px;
  width: auto;
}

@media screen and (max-width: 800px) { 
  .navbar-tabs {
    width: auto;
    min-width: 350px;
  }

  /* Players tab needs to scroll to see all stats */
  #react-tabs-3 > div {
    overflow: scroll;
  }

  #react-tabs-12, #react-tabs-14 {
    display: none;
  }

  .navbar-tabs > div, .navbar-tabs > ul {
    width: 100vw;
  }

  ul.react-tabs__tab-list:last-child, li.react-tabs__tab > div {
    display: none;
  }

  .react-tabs__tab-panel {
    padding: 0;
  }

  th {
    width: auto;
  }
}
