@import url('https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap');

:root {
  --main-bg: #4484CE;
  --main-text:  #1b2733;
  --gradient1: #D9D9D9;
  --orange: #F19F4D;
  --yellow: #F9CF00;

  --main-very-dark: rgb(9, 1, 56);
  --main-dark: #4484CE;
  --main-even: #a1c1e6;
  --main-even2: #7da5d3;
  --main-light: #D9D9D9;
  --alt-text: var(--yellow);

  --link: var(--main-very-dark);


  /* --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5); */

  --header-text: #ffffff;
  --button-bg: var(--main-dark);
  --disabled:rgba(87, 87, 128, 0.5);
  --success: #00ff99;
  --success-hover: #1cba7b;
  --error: rgba(238, 11, 11, 0.651);
  --error-hover: rgba(197, 31, 78, 0.651);
}

/* 🎨 colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: flex;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: var(--main-light);;    
  /* grid-template-columns: minmax(100%, 10%); */
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  margin: 0;
  width: calc(100vw - 270px);
  /* padding-right: 22vw; */
  min-width: 600px;
  /* margin: 0 auto; */
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

a {
  color: var(--link);
}

a:hover {
  color: var(--main-dark);
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  margin: 50px auto;
  padding: 5px;
  background: var(--main-dark);
  max-width: 100%;
  color: var(--main-light);;
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: var(--main-dark);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

button {
  border-radius: 4px;
  padding: 4px;
  border: 1px solid var(--main-light);
  color: var(--main-light);
  font-weight: 800;
  background: var(--button-bg);
}

button:disabled {
  background-color: var(--main-light);
  color: var(--disabled);
}

.save-button {
  max-width: 100px;
  margin: 15px auto;
  color: var(--main-text);
}

.save-button:not(:disabled) {
  color: var(--main-light);
  background:var(--main-dark);
}

.button-large {
  font-size: 1.4rem;
  padding: 7px 15px;
}

button:not(:disabled):hover {
  background: var(--main-even);
  border: 1px solid var(--main-even);
  color: var(--main-very-dark);
}

.hidden {
  display: none;
}

.margin-15 {
  margin: 15px;
}

.margin-50 {
  margin: 50px;
}

textarea {
  width: 90%;
}

.new-rules-button {
  margin-bottom: -25px;
}

@media (max-width: 800px) {
  main {
    margin-top: 55px;
    width: 100vw;
    min-width: 350px;
    overflow-x: hidden;
  }

  main > div {
    width: 100%;
  }
}
